-box;\n        border-radius: 2px;\n        margin-right: 4px;\n\n        &__title {\n          font-style: normal;\n          font-weight: 400;\n          color: #5370ff;\n        }\n      }\n\n      .couponIcon {\n        width: 24px;\n        height: 16px;\n        position: relative;\n        // background: url(~global/assets/img/coupon_icon.svg) no-repeat;\n\n        .saleInfo {\n          margin-bottom: 4px;\n        }\n      }\n\n      .couponFoldContent {\n        padding: 8px;\n        background-color: #fff;\n        position: absolute;\n        bottom: 23px;\n        left: 50%;\n        transform: translateX(-50%);\n        box-shadow: 0 2px 6px rgba(0, 23, 54, .08);\n        border-radius: 4px;\n        white-space: nowrap;\n\n        &.hidden {\n          visibility: hidden;\n        }\n\n        .arrow {\n          width: 0;\n          height: 0;\n          border-top: 10px solid #fff;\n          border-left: 10px solid transparent;\n          border-right: 10px solid transparent;\n          position: absolute;\n          bottom: -4px;\n          left: 50%;\n          transform: translateX(-50%);\n        }\n      }\n\n      .saleInfo {\n        height: 16px;\n        background: rgba(254, 44, 85, .04);\n        border: 1px solid rgba(254, 44, 85, .24);\n        border-radius: 2px;\n        font-size: 11px;\n        line-height: 16px;\n        color: #fe2c55;\n        white-space: nowrap;\n        display: flex;\n        align-items: center;\n\n        &__campaignType {\n          padding: 0 4px;\n          border-right: .5px dashed rgba(254, 44, 85, .24);\n        }\n\n        &__title {\n          padding: 0 3.5px;\n        }\n      }\n    }\n\n    &__activity :last-child {\n      margin-right: 0;\n    }\n\n    &__rankSales {\n      display: inline-flex;\n      align-items: center;\n      padding: 0 4px 0 1px;\n      height: 18px;\n      width: auto;\n      background: linear-gradient(90.22deg, #fff4e5 -23.56%, #ffedee 100.96%);\n      box-shadow: in